<template>
  <div class="appJ" style="background-color: #FFF5EB">
   

      <Header></Header>

      
    <v-main  color="#FFF5EB" >
      <v-row dense class="px-4 py-4">
        <v-col cols="12 px-4 py-4">
          <v-card
              color="#fff"
             
          >

            <v-btn  block class="warning ">
              <h3>ລາຍການການເຕີມງິນ </h3>
            </v-btn>

 <v-row class="center px-4 py-4">
    <v-dialog
      v-model="dialog"
      persistent
      max-width="291px"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-col>
        <v-text-field
        v-model="dateRangeText"
        label="ເລືອກມື້"
        color="warning"
        prepend-icon="mdi-calendar"
        readonly
        v-bind="attrs"
          v-on="on"
      ></v-text-field>
    </v-col>
      </template>
      <v-card>
        
       
   
      <v-date-picker
        v-model="dates"
        range
        color="warning"
      ></v-date-picker>

         
     
        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn
            color="red darken-1"
            text
            @click="dialog = false"
          >
            ປິດ
          </v-btn>
          <v-btn
            color="green darken-1"
            text
            @click="search"
          >
           ຄົ້ນຫາ
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </v-row>
 


            <v-simple-table class="px-4 py-4">
              <template v-slot:default>
                <thead class="warning "> 
                <tr >
                    <th class="text-left white--text">
                 
                  </th>
                  <th class="text-left white--text">
                   ເວລາ
                  </th>
                  <th class="text-left white--text">
                ຈໍານວນເງີນ
                  </th>
                    <th class="text-left white--text">
                  ຈໍານວນເງີນໃນຄັງຕອນແລກ
                  </th>
                   <th class="text-left white--text">
                 ຈໍານວນເງີນໃນຄັງພາຍຫຼັງ
                  </th>

                 
                  <th class="text-left white--text">
                  ປະເພດ
                  </th>
                
                  
                 
                </tr>
                </thead>
                <tbody>
                <tr
                    v-for="(item,index) in transactionHistories"
                    :key="index"
                >
                        <td>

<v-btn
v-if="item.amount > 0"
   
     color="green white--text"
    
     icon
    >
     <v-icon>mdi-arrow-right-bold-box</v-icon>
    </v-btn>
    
   <v-btn
v-else
       color="red white--text"
    icon
     
  
    >
     <v-icon>mdi-arrow-left-bold-box</v-icon>
    
    </v-btn>



                     </td>
                  <td class="warning--text">{{item.created_at}}</td>
                  <td class="warning--text">{{item.amount_show}} {{item.currency}}</td>
                 <td class="warning--text">{{item.current_balance}} {{item.currency}}</td>
              
                       <td class="warning--text">{{item.new_balance}} {{item.to_currency}}</td>
                    <td class="warning--text">{{item.type}}</td>
                    
             
                </tr>
                </tbody>
              </template>
            </v-simple-table>


          </v-card>
        </v-col>




      </v-row>

    </v-main>



  </div>
</template>

<script>
import Header from '../components/Header'
export default {
  mounted() {
   this.getTransactionHistory()
  },
  data () {
    return {
      transactionHistories :[],
      start:'',
      end:'',
      statusError:null,
      statusSuccess:null,
      dates: [],
          dialog: false,
    }
  },
  computed:{
  dateRangeText () {
        return this.dates.join(' ~ ')
      },
  },
 methods:{

      search(){
        this.start =this.dates[0]
        this.end = this.dates[1]
        this.getTransactionHistory()
      
      },
     
     async getTransactionHistory(){
      var api = `${process.env.VUE_APP_WEB_API_URL}report-fund`;
      await this.axios.post(api,
          {
            'start' : this.start,
            'end' : this.end
          },{
            headers: {
              Authorization: `Bearer ${localStorage.token}`,
              "Content-Type": "application/json",
            }
          })
          .then(response => {
            this.transactionHistories = response.data.data
          })
          .catch(e => {
            if (e.response.status == 422 || e.response.status == 500){
              this.statusError =e.response.data.msg
            }
            if (e.response.status == 404) {
              this.statusError = "Not found Api"
            }
            if (e.response.status == 401) {
              window.location.href = '/'
              this.statusError = "auth"
            }
          });
    },
 },

  components: {Header},
  watch:{
    statusError(value){
      if (value != null){
        setTimeout(() => (this.statusError = null), 3000)
      }
    },

    statusSuccess(value){
      if (value != null){
        setTimeout(() => (this.statusSuccess = null), 3000)
      }
    },

  }

}
</script>

