<script>
import Header from '../components/Header'
import { ModelListSelect } from 'vue-search-select'


export default {
  mounted() {
    this.getExchangeRate()
    this.getCustomer()
    this.getTypeExchange()
    this.getCurrencies()
    this.getCurrenciesOutDay()
    this.getTransactionHistory()
  },
  data () {
    return {
      desserts: [
        {
          name: 'Frozen Yogurt',
          calories: 159,
        },
      ],
      webUrl : `${process.env.VUE_APP_WEB_URL}`,
      exchangeRates:[],
      customers:[],
      typeExchanges:[],
      currencies :[],
      currenciesOutDay :[],
      transactionHistories:[],
      statusError:null,
      statusSuccess:null,
      sheet: false,
      dialog: false,
      delete_tran_id:null,
      customer_id: '',

      exchange_rate_id: '',
      from_currency : '',
      to_currency : '',
      rate : '',
      rate_old: '',
      rate_vip:'',
      type_currency:'',
      customer_type:0,

      type_exchange_id:1,
      customer_name:'',
      customer_phone:'',
      amount:0,
      amount_old:0,
      selectCustomer:''

    }
  },
  computed:{
      getAmountExchange: function(){

        if(this.customer_type == 0){
        return this.amount
        }else{
          return 9
        }
        
      },
  },
  methods:{
    setDeleteTransaction(id){
     this.delete_tran_id = id
     this.dialog= true
    } ,
        async cancle(){
      var api = `${process.env.VUE_APP_WEB_API_URL}cancle-transaction-history`;
      await this.axios.post(api,
          {
            'id' : this.delete_tran_id
          },{
            headers: {
            Authorization: `Bearer ${this.$store.getters.token}`,
              "Content-Type": "application/json",
            }
          })
          .then(response => {
             this.dialog= false
              this.statusSuccess =response.data.msg
              this.getCurrencies()
            this.getCurrenciesOutDay()
            this.getTransactionHistory()
           
          })
          .catch(e => {
            if (e.response.status == 422 || e.response.status == 500){
              this.statusError =e.response.data.msg
            }
            if (e.response.status == 404) {
              this.statusError = "Not found Api"
            }
            if (e.response.status == 401) {
              window.location.href = '/'
              this.statusError = "auth"
            }
          });
    },



    codeAndNameAndDesc (item) {
  
      return `${item.id} - ${item.name} - ${item.phone}`
    },
    selectFromParentComponent1 (item) {
      this.customer_id = item.id,
      this.customer_type = item.name
    },

    customerReset(){
          this.customer_id= ''
          this.customer_name=''
          this.customer_phone=''
          this.selectCustomer=''
    },

    showExchangeCurrency (item) {
      return ` ${item.from_currencies_code}/${item.to_currencies_code}  ( ${item.from_currencies}/${item.to_currencies}) - ${item.rate_show}`
    },
    selectFromParentComponent2 () {
      this.exchange_rate_id = this.options[0].rate_id,
      this.from_currency = this.options[0].from_currencies,
      this.to_currency = this.options[0].to_currencies,
      this.rate = this.options[0].rate,
      this.rate_old = this.options[0].rate_old,
      this.rate_vip = this.options[0].rate_old,
      this.type_currency = this.options[0].type
    },

     async getTransactionHistory(){
      var api = `${process.env.VUE_APP_WEB_API_URL}transaction-history`;
      await this.axios.post(api,
          {
            //data
          },{
            headers: {
           Authorization: `Bearer ${this.$store.getters.token}`,
              "Content-Type": "application/json",
            }
          })
          .then(response => {
            this.transactionHistories = response.data.data
          })
          .catch(e => {
            if (e.response.status == 422 || e.response.status == 500){
              this.statusError =e.response.data.msg
            }
            if (e.response.status == 404) {
              this.statusError = "Not found Api"
            }
            if (e.response.status == 401) {
              window.location.href = '/'
              this.statusError = "auth"
            }
          });
    },

