    async submitExchange(){
      var api = `${process.env.VUE_APP_WEB_API_URL}submit-exchange`;

      if (this.amount === ''){
        this.amount = 0
      }
      if (this.amount_old === ''){
        this.amount_old = 0
      }

      await this.axios.post(api,
          {
            'amount' : this.amount,
            'amount_old' : this.amount_old,
            'type_exchange_id' : this.type_exchange_id,
            'exchange_rate_id' : this.exchange_rate_id,
            'customer_id' : this.customer_id,
            'customer_name' : this.customer_name,
            'customer_phone' : this.customer_phone
          },{

            headers: {
            Authorization: `Bearer ${this.$store.getters.token}`,
              "Content-Type": "application/json",
            }
          })
          .then(response => {
            this.sheet = false
            this.customerReset()
            this.statusSuccess =response.data.msg
            this.getCurrencies()
            this.getCurrenciesOutDay()
            this.getTransactionHistory()
          })
          .catch(e => {
            if (e.response.status == 422 || e.response.status == 500){
              this.statusError =e.response.data.msg
            }

            if (e.response.status == 404) {
              this.statusError = "Not found Api"
            }
            if (e.response.status == 401) {
              window.location.href = '/'
              this.statusError = "auth"
            }

          });
    },

