<template>
<div style="background-color: #FFF5EB">
    <v-app-bar
        src="https://www.ofx.com/wp-content/uploads/2021/07/gca-header.png"
    >

      <v-btn
          large

          @click.stop="drawer = !drawer">  <v-icon color="#FF850A">mdi-format-align-justify</v-icon></v-btn>

      <v-spacer></v-spacer>
      <v-btn
          class="mx-2"
          outlined
          color="#fff"
          large ><h2>ສາຂາ {{branch}} </h2> </v-btn>

      <v-spacer></v-spacer>

      <div class="text-primary">

      </div>
      <v-btn
          class="mx-2"
          outlined
          color="#fff"
          small

      >

        <v-icon color="#fff">mdi-account</v-icon>{{name}}

      </v-btn>


      <v-btn
          class="mx-2"
          fab
          small
          @click="logout()"
      >

        <v-icon color="#FF850A">mdi-export</v-icon>

      </v-btn>

    </v-app-bar>

    <v-navigation-drawer
        v-model="drawer"
        absolute
        bottom
        temporary
    >
      <v-list
          nav
          dense
      >
        <v-list-item-group
            v-model="group"
            active-class="deep-purple--text text--accent-4"
        >
        <router-link to="/dashboard">
          <v-list-item>
            <v-list-item-title>ໜ້າຫຼັກ</v-list-item-title>
          </v-list-item>
</router-link>
<router-link to="/fund">
          <v-list-item>
            <v-list-item-title>ລາຍງານການເຕີມເງີນ</v-list-item-title>
          </v-list-item>
          </router-link>
<router-link to="/transaction">
          <v-list-item>
            <v-list-item-title>ລາຍງານແລກປ່ຽນເງີນຕາ</v-list-item-title>
          </v-list-item>
</router-link>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>

  <v-alert
      border="top"
      dark
      v-if="statusError!=null"
  >
    {{statusError}}
  </v-alert>

</div>

</template>
<script>
export default {
  mounted() {
    this.getEmployeeInfo()
    this.checkLogout()
  },
  data:()=>{
    return{
      data :[],
      branch:'',
      name:'',
      statusError:null,
      drawer: false,
      group: null,
    

    }
  },

  methods:{
    async checkLogout(){
      if (!localStorage.token){
        window.location.href = '/'
      }
    } ,

    async getEmployeeInfo(){

      var api = `${process.env.VUE_APP_WEB_API_URL}employee-info`;
      await this.axios.post(api,
          {
            
          },{

            headers: {
              Authorization: `Bearer ${this.$store.getters.token}`,
              "Content-Type": "application/json",
            }
          })
          .then(response => {
             this.data = response.data
            this.branch = response.data.data.branch
            this.name = response.data.data.name
            this.token = localStorage.token
           
          })
          .catch(e => {
            if (e.response.status == 422 || e.response.status == 500){
              this.statusError =e.response.data.msg
            }

            if (e.response.status == 404) {
              this.statusError = "Not found Api"
            }
            if (e.response.status == 401) {
              this.statusError = "auth"
            }
            // window.location.href = '/'
          });

    },

    async logout(){
      var api = `${process.env.VUE_APP_WEB_API_URL}logout`;
      await this.axios.post(api,
          {
       //data
          },{

            headers: {
     Authorization: `Bearer ${this.$store.getters.token}`,
              "Content-Type": "application/json",
            }
          })
          .then(response => {
            response.data
            window.location.href = '/'
          })
          .catch(e => {
            if (e.response.status == 422 || e.response.status == 500){
              this.statusError =e.response.data.msg
            }

            if (e.response.status == 404) {
              this.statusError = "Not found Api"
            }
            if (e.response.status == 401) {
              this.statusError = "auth"
            }
            window.location.href = '/'
          });
    }
  },
  // watch:{
  //   statusError(value){
  //     if (value != null){
  //       setTimeout(() => (this.statusError = null), 2000)
  //     }
  //   },
  //   group () {
  //     this.drawer = false
  //   },
  // }
  }
</script>

