<template>
<div >

  <v-app>
    <v-app-bar
        app
        color="warning lighten-1"
        dark
    >
      <div class="d-flex align-center">

        <v-img
            alt="Vuetify Logo"
            class="shrink mr-2"
            contain
            src="https://cdn.vuetifyjs.com/images/logos/vuetify-logo-dark.png"
            transition="scale-transition"
            width="40"
        />
        ລະບົບແລກປ່ຽນເງີນຕາ
      </div>




    </v-app-bar>

    <v-main id="jBg">

        <v-card
            class="mx-auto my-10 "
            max-width="500"
            :loading="loading"
        >
          <div class="text-center ">
            <h1 class="warning--text pt-2">ເຂົ້າສູລະບົບ</h1>
          </div>


          <template slot="progress">
            <v-progress-linear
                color="orange accent-2"
                height="10"
                indeterminate
            ></v-progress-linear>
          </template>
          <v-alert
              border="top"
              color="red lighten-2"
              dark
              v-if="statusError!=null"
          >
            {{statusError}}
          </v-alert>
          <v-card

              class="py-5 px-3"
              ref="form"
              v-model="valid"
              lazy-validation
          >
            <v-text-field
                ref="focusMe"
                autocomplete="off"
                v-model="username"
                :rules="usernameRules"
                label="ຊື່ບັນຊີ"
                color="orange accent-2"
                required
                outlined
            ></v-text-field>

            <v-text-field

                :append-icon="value ? 'mdi-eye' : 'mdi-eye-off'"
                @click:append="() => (value = !value)"
                :type="value ? 'password' : 'text'"
                v-model="password"
                :rules="passwordRules"
                label="ລະຫັດຜ່ານ"
                color="orange accent-2"
                required
                outlined
            ></v-text-field>

            <v-checkbox
                v-model="checkbox"
                :rules="[v => !!v || 'You must agree to continue!']"
                label="Do you agree?"
                required
            ></v-checkbox>
            <v-card-actions>
              <v-btn
                  @click="testApi"
                  color="warning"
                  block
              >
                ເຂົ້າສູລະບົບ {{$store.getters.token}}
              </v-btn>
            </v-card-actions>

          </v-card>


        </v-card>



    </v-main>


<!--    <v-footer-->
<!--        color="warning"-->
<!--        padless>-->
<!--      <v-col-->

<!--          class="text-center"-->
<!--          cols="12"-->
<!--      >-->
<!--        {{ new Date().getFullYear() }} — <strong>Exchange Money </strong>-->
<!--      </v-col>-->
<!--    </v-footer>-->
  </v-app>
</div>
</template>

<script>


  export default {
    mounted () {
      this.$refs.focusMe.focus()
    },
    data: () => ({
      value: String,
      statusError:null,
      errors: [],
      loading: false,
      selection: 1,
      valid: true,
      username: '',
      usernameRules: [
        v => !!v || 'Name is required',

      ],
      password: '',
      passwordRules: [
        v => !!v || 'password is required',

      ],

      checkbox: false,

    }),

    methods: {
     async testApi(){

         var api = `${process.env.VUE_APP_WEB_API_URL}login`;
       setTimeout(() => (this.loading = false), 2000)
     await this.axios.post(api,
         {
           username: this.username,
           password: this.password
         },{

          headers: {
            "Content-Type": "application/json",
          }
        })
            .then(response => {
              localStorage.setItem('token',response.data.data.token)
              this.$store.commit('setToken',response.data.data.token)
              this.$router.push('Dashboard')
            })
          .catch(e => {
            if (e.response.status == 422 || e.response.status == 500){
              this.statusError =e.response.data.msg
            }
            if (e.response.status == 404) {
              this.statusError = "Not found Api"
            }

          });

      },
    },
    computed:{

    },
    watch:{
      statusError(value){
        if (value != null){
          setTimeout(() => (this.statusError = null), 2000)
        }
      }
    }
  }

</script>

